.NavLeft {
  height: 100%;

  /* 覆盖antd组件样式 */
  .ant-menu {
    height: 100%;
    border-right: 0px;
    background: #6A3FF3;
    color: #fff;

    &:not(.ant-menu-horizontal) {

      .ant-menu-item-selected,
      .ant-menu-item-active {
        background-color: #522DCB;
        color: #fff;
        opacity: 1;
      }
    }

  }

  .ant-menu-inline .ant-menu-item {
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 16px;
    margin: 0;
    padding: 0 !important;
    opacity: 0.7;

    &::after {
      right: 0;
      top: 50%;
      width: 0;
      height: 0;
      transform: translateY(-9px);
      border-top: 9px solid transparent;
      border-right: 10px solid #fff;
      border-bottom: 9px solid transparent;
    }

    a {
      color: #fff;
    }
  }

  /* antd组件样式修改结束 */
  .icon {
    position: absolute;
    left: 32px;
    top: 19px;
    width: 24px;
    height: 24px;
    background: no-repeat center center;
    background-size: 24px 24px;
  }

  .sub-icon {
    position: absolute;
    left: 32px;
    top: 10px;
    width: 24px;
    height: 24px;
    background: no-repeat center center;
    background-size: 24px 24px;
  }

  .icon-newActive {
    background-image: url('~@/assets/newActive.png');
  }

  .icon-myActive {
    background-image: url('~@/assets/myActive.png');
  }

  .txt {
    margin-left: 68px;
  }
  .sub-txt {
    font-size: 16px;
    margin-left: 44px;
  }
}