.myActive {
  padding: 20px 30px 0 30px;

  .myact-title {
    margin-bottom: 20px;
    font-family: PingFang-SC-Bold;
    font-size: 20px;
    color: #333;
  }

  .act-table {
    font-size: 14px;
    white-space: nowrap;
    // .ant-table {
    border-radius: 4px;
    border: 1px solid #e8e8e8;
    border-bottom: 0;

    // }
    .ant-table-tbody {
      background-color: #fff;
    }

    .ant-table-tbody>tr>td {
      background: rgba(255, 255, 255, 0) !important;
    }

    .ant-table-tbody>tr:hover {
      background: #f5f5f5 !important;
    }

    .act-name {

      .icon-qrcode {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin-left: 4px;
        width: 24px;
        height: 24px;
        background: url('~@/assets/icon-qrcode.png') no-repeat center center;
        background-size: 24px 24px;
      }

      .icon-qrcode:hover {
        cursor: pointer
      }

      .act-simg {
        margin: 0 11px;
        width: 70px;
        height: 42px;
      }

      .act-title {
        display: inline-block;
        vertical-align: middle;
        height: 20px;
        line-height: 20px;
      }
    }
  }

  .qrCode {
    width: 168px;
    height: 210px;
    background: #FFFFFF;
    border-radius: 4px;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 12px;
    position: fixed;
    text-align: center;
    border: 1px solid #9797975c;
    z-index: 99;

    p {
      margin-top: 16px;
      display: flex;
      align-items: center;
      justify-content: center;

      i {
        width: 17px;
        height: 14px;
        background: url('../../../assets/wechat.png') no-repeat;
        background-size: contain;
      }

      span {
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #666666;
        text-align: justify;
      }
    }
  }

  .ant-layout {
    background: #f4f4f4;
  }
}

.ant-modal-body {
  width: 416px !important;
  height: 160px !important;
}