.Award {
  height: 100%;
  /* 礼包页加减按钮 */
  .icon-add,
  .icon-subtract {
    position: absolute;
    bottom: 16px;
    z-index: 10;
    width: 20px;
    height: 20px;
    background-size: 100% 100%;
  }
  .icon-add {
    right: 20px;
    background: url('~@/assets/icon-add.png') no-repeat center;
  }
  .icon-subtract {
    right: 46px;
    background: url('~@/assets/icon-subtract.png') no-repeat center;
  }
  .award-title {
    position: relative;
    width: 770px;
    height: 90px;
    background: #F5F5F5;
    border: 1px solid #D9D9D9;
    .title-txt {
      margin: 16px 0 0 20px;
      font-size: 14px;
      color: #333;
    }
  }
  /* 第一层奖项tab */
  // .ant-form {
    > .ant-tabs {
      top: -39px;
      width: 770px;
      border: 1px solid #D9D9D9;
      border-top: 0;
      /* 奖项tab页头 */
      > .ant-tabs-bar {
        border-color: #fff;
        margin-left: 20px;
        margin: 0 70px 0px 20px;
        > .ant-tabs-nav-container {
          > .ant-tabs-nav-wrap {
            > .ant-tabs-nav-scroll {
              > .ant-tabs-nav {
                .ant-tabs-tab {
                  border-color: transparent;
                  background: transparent;
                  // padding: 9px 17px 11px;
                  font-size: 14px;
                  color: #666;
                  border-radius: 0;
                  border-bottom: 0;
                  .ant-tabs-close-x {
                    display: none;
                  }
                }
                .ant-tabs-tab-active,
                .ant-tabs-tab:hover {
                  color: #6485FF;
                }
                .ant-tabs-tab-active {
                  border: 1px solid #D9D9D9;
                  border-bottom: 0;
                  background: #fff;
                }
              }
            }
          }
        }
      }
      /* 奖项tab内容 */
      > .ant-tabs-content {
        > .ant-tabs-tabpane {
          background: #fff;
          padding: 30px 0 0 20px;
          .pack-code {
            margin-bottom: 20px;
            .pack-code-txt {
              margin-right: 31px;
            }
            .pack-code-num {}
          }
        }
        /* 奖项标题虚线 */
        .set-name {
          font-size: 16px;
          color: #666;
          margin-bottom: 20px;
          .dashed-border {
            margin-left: 13px;
            vertical-align: middle;
            display: inline-block;
            height: 0;
            width: 619px;
            border-bottom: 1px dashed #cbcbcb;
          }
        }
        label.ant-form-item-no-colon {
          color: #999;
        }
        /* 奖项标题虚线end */
        .pack-set {
          width: 730px;
          height: 275px;
          > .ant-row {
            #radio-group {
              margin-left: 15px;
            }
            #total {
              margin-left: 15px;
            }
          }
          .row-two {
            > .ant-row {
              width: 50%;
              display: inline-block;
              vertical-align: middle;
              &:first-child {
                // margin-right: 70px;
              }
              
            }
          }
          
        }
        .ant-input {
          width: 170px;
          height: 40px;
        }
        .ant-select-selection {
          width: 170px;
          height: 40px;
        }
        
      }
    }
  // }
}

// 删除弹窗样式
.modal-min-del {
  width: 416px!important;
  // height: 163px;
  .ant-modal-content {
    .ant-modal-body {
      height: 110px!important;
      .delete-txt {
        height: 24px;
        line-height: 24px;
        margin-top: 20px;
        margin-left: 10px;
        font-size: 16px;
        // color: #333;
        .icon-ques {
          font-size: 22px;
          margin-right: 15px;
          color: #faad14;
        }
      }
    }
    .ant-modal-footer {
      border: 0;
      padding-bottom: 20px;
      padding-right: 22px;
      .ant-btn {
        &:first-child:hover {
          color: rgba(0, 0, 0, 0.65);
          border-color: #d9d9d9;
        }
      }
      .ant-btn-primary {
        margin-left: 20px;
        border-color: #6A3FF3;
        background-color: #6A3FF3;
        border-radius: 4px;
      }
    }
  }
}