.GiftTab {
  width: 730px;
  position: relative;
  .icon-add {
    top: 48px;
    bottom: 0;
    right: 0;
  }
  .icon-subtract {
    top: 48px;
    bottom: 0;
    right: 26px;
  }
  /* 第二层礼品tab */
  > .ant-tabs {
    /* 奖项tab页头 */
    > .ant-tabs-bar {
      // border-color: #fff;
      // margin-left: 20px;
      // margin: 0 70px 0px 20px;
      padding-right: 50px;
      > .ant-tabs-nav-container {
        > .ant-tabs-nav-wrap {
          > .ant-tabs-nav-scroll {
            > .ant-tabs-nav {
              .ant-tabs-tab {
                // position: relative;
                // border-color: transparent;
                background: transparent;
                padding: 0 24px;
                font-size: 14px;
                color: #666;
                line-height: 40px;
                border: 0;
                .ant-tabs-close-x {
                  display: none;
                }
                &:not(:last-child) :after {
                  position: absolute;
                  right: 0;
                  top: 13px;
                  content: '';
                  display: block;
                  height: 14px;
                  width: 1px;
                  background-color: #D9D9D9;
                }
              }
              .ant-tabs-tab-active,
              .ant-tabs-tab:hover {
                color: #6485FF;
                font-weight: normal;
              }
              .ant-tabs-tab-active {
                border-bottom: 3px solid #6485FF;
              }
              > .ant-tabs-ink-bar {
                // visibility: visible;
                bottom: 0;
                height: 3px;
                background: #6485FF;
              }
            }
          }
        }
      }
    }
    > .ant-tabs-content {
      .gift-set {
        width: 730px;
        padding-bottom: 20px;
        .select-option {
          .ant-row {
            display: inline-block;
          }
          a {
            display: inline-block;
            margin-top: 10px;
            margin-left: 20px;
          }
        }
        .row-two {
          > .ant-row {
            width: 50%;
            display: inline-block;
            vertical-align: middle;
            &:first-child {
              // margin-right: 70px;
            }
          }
        }
        .row-three {
          > .ant-row {
            width: 33.33%;
            display: inline-block;
            vertical-align: middle;
            &:first-child {
              // margin-right: 70px;
            }
            
          }
        }
        .set-name {
          .dashed-border {
            margin-left: 21px;
            width: 644px;
          }
        }
        .quan-show {
          .show-item {
            min-width: 200px;
            display: inline-block;
            // margin-right: 60px;
            .item-label {
              line-height: 40px;
              color: #999;
            }
            .item-txt {
              line-height: 40px;
              color: #666;
            }
          }
        }
      }
    }
  }
}