.register-bg {
  position: relative;
  width:100%;
  height:100%;
  background:url('../../assets/backg.png') 0px 0px no-repeat;
  background-size: 100% 100%;
  margin:0 auto;
  .register-header{
    background:#FFFFFF ;
    height:70px;
    .header-img{
        width:100px;
        height:48px;
        background:#FFFFFF url('../../assets/logo.png') no-repeat;
        margin:11px 0 0 24px;
    }
  }
  .register-container{
    width:460px;
    // height:310px;
    background:#fff;
    margin:170px auto 0;
    box-shadow: 0 4px 10px 0 rgba(0,0,0,0.30);
    border-radius: 10px;
    padding-top:0.1px;
  }
  .footer-text{
    width: 100%;
    position: absolute;
    bottom: 50px;
    text-align: center;
    font-size: 12px;
    color: #FFF;
    // margin:363px auto 0;
  }
 
}