.Share {
  .share-title {
    position: relative;
    width: 770px;
    height: 90px;
    background: #F5F5F5;
    border: 1px solid #D9D9D9;
    .title-txt {
      margin: 16px 0 0 20px;
      font-size: 14px;
      color: #333;
    }
    .shareForm-btn {
      position: absolute;
      right: 27px;
      top: 27px;
    }
  }
  /* 图片上传组件样式 */
  .img-uploader  {
    > .ant-upload {
      width: 150px;
      height: 150px;
      border-radius: 10px;
      overflow: hidden;
      &.ant-upload-select-picture-card {
        margin: 0;
      }
      >.ant-upload {
        // display: inline-block;
        text-align: center;
        padding: 0;
        position: relative;
        .img-choose {
          width: 100%;
          height: 26px;
          position: absolute;
          left: 0;
          bottom: 0;
          color: rgba(255, 255, 255, 0.9);
          background-color: rgba(0, 0, 0, 0.3);
          font-size: 12px;
          line-height: 26px;
        }
        .icon-imgupload {
          position: absolute;
          top: 35px;
          left: 58px;
          width: 35px;
          height: 31px;
          background: url('~@/assets/icon-imgupload.png') no-repeat center;
          background-size: 100% 100%;
        }
        .ant-upload-text {
          position: absolute;
          left: 25px;
          bottom: 28px;
          width: 100px;
          height: 28px;
          background: #6A3FF3;
          border-radius: 2px;
          font-size: 14px;
          color: #FFF;
          line-height: 28px;
        }
      }
    }
  }

  .ant-form {
    > .ant-tabs {
      top: -39px;
      width: 770px;
      > .ant-tabs-content {
        // height: 120px;
        // margin-top: -16px;
        > .ant-tabs-tabpane {
          background: #fff;
          padding: 30px 0 0 20px;
        }
      }
      > .ant-tabs-bar {
        border-color: #fff;
        margin-left: 20px;
        .ant-tabs-tab {
          border-color: transparent;
          background: transparent;
          // padding: 9px 17px 11px;
          font-size: 14px;
          color: #666;
          border-radius: 0;
          border-bottom: 0;
        }
        .ant-tabs-tab-active,
        .ant-tabs-tab:hover {
          color: #6485FF;
        }
        .ant-tabs-tab-active {
          border: 1px solid #D9D9D9;
          border-bottom: 0;
          background: #fff;
        }
      }
    }
  }
  .share-tab {
    /* 链接分享 */
    .link-preview {
      margin-bottom: 40px;
      .pre-img {
        position: relative;
        width: 300px;
        // height: 120px;
        background-color: #f5f5f5;
        padding: 18px 10px 18px 18px;
        border-radius: 6px;
        .pre-avatar {
          width: 38px;
          height: 38px;
          border-radius: 6px;
          float: right;
          background: #fff url('~@/assets/bg-avatar.png') no-repeat center;
          background-size: 100% 100%;
        }
        .inner {
          width: 220px;
          padding: 10px;
          background-color: #fff;
          border-radius: 4px;
          .title {
            word-wrap: break-word;
            word-break: break-all;
            font-size: 16px;
            color: #333;
            margin-bottom: 6px;
          }
          .pre-con {
            .con-txt {
              width: 145px;
              color: #999;
              float: left;
              word-wrap: break-word;
              word-break: break-all;
            }
            .con-img {
              float: right;
              width: 44px;
              height: 44px;
            }
            .def-link-img {
              float: right;
              width: 44px;
              height: 44px;
              background: url('~@/assets/bg-linkpic.png') no-repeat center;
              background-size: 100% 100%;
            }
          }
        }
      }
    }
    .act-name {
      .ant-input {
        width: 428px;
        height: 40px;
      }
    }
    .upload-img {
      > .ant-form-item {
        display: inline-block;
      }
      .upload-desc {
        display: inline-block;
        width: 257px;
        height: 180px;
        margin-top: 38px;
        margin-left: 20px;
        word-wrap: break-word;
        word-break: break-all;
        overflow: hidden
      }
    }
    
    /* 长图分享 */
    .long-pic {
      .share-switch {
        margin-bottom: 20px;
        .relSwitch-desc {
          margin: -23px 0 0 124px;
          width: 426px;
        }
      }
      .long-preview {
        width: 300px;
        float: left;
        .long-pre-img {
          border-radius: 6px;
          overflow: hidden;
          width: 240px;
          // height: 368px;
          background-color: #f5f5f5;
          img {
            width: 100%;
          }
          .def-long-img {
            width: 100%;
            height: 358px;
            background: url('~@/assets/bg-longpic.jpg') no-repeat center;
            background-size: 100% 100%;
          }
        }
        
      }
      .upload-img {
        float: left;
        > .ant-form-item {
          display: inline-block;
        }
        .upload-desc {
          display: inline-block;
          width: 257px;
          height: 200px;
          margin-top: 40px;
          margin-left: 15px;
          word-wrap: break-word;
          word-break: break-all;
          overflow: hidden
        }
      }
    }

    /* 小程序分享 */
    .miniPro-pic {
      .set-name {
        margin-bottom: 25px;
        .dashed-border {
          margin-left: 21px;
          vertical-align: middle;
          display: inline-block;
          height: 0;
          width: 626px;
          border-bottom: 1px dashed #cbcbcb;
        }
      }

      .upload-img {
        width: 434px;
        position: absolute;
        right: 0;
        top: 34px;
        > .ant-form-item {
          display: block;
        }
        .upload-desc {
          margin: 0;
          position: absolute;
          right: 0;
          top: 140px;
          width: 257px;
        }
      }

      .miniPro-shareCard {
        position: relative;
        margin-bottom: 50px;
        
        .miniPro-preview {
          width: 300px;
          display: inline-block;
          .miniPro-pre-img {
            border-radius: 6px;
            width: 218px;
            padding: 10px;
            min-height: 260px;
            background-color: #f5f5f5;
            .container {
              border-radius: 4px;
              background-color: #fff;
              padding: 10px;
              padding-bottom: 0;
              .con-top {
                padding-left: 23px;
                position: relative;
                .sn-logo {
                  position: absolute;
                  left: 0;
                  top: 1px;
                  width: 18px;
                  height: 18px;
                  border-radius: 50%;
                  background: url('~@/assets/icon-snlogo.png') no-repeat center;
                  background-size: 100% 100%;
                  font-size: 12px;
                  color: #999;
                }
              }
              .title {
                font-size: 14px;
                color: #333;
                margin: 6px 0;
                word-wrap: break-word;
                word-break: break-all;
                overflow: hidden;
              }
              .pre-img {
                width: 100%;
                margin-bottom: 8px;
              }
              .def-pre-img {
                width: 100%;
                height: 143px;
                background: url('~@/assets/bg-minicard.png') no-repeat center;
                background-size: 100% 100%;
                margin-bottom: 8px;
              }
              .con-btm {
                position: relative;
                height: 24px;
                padding-left: 18px;
                border-top: 1px solid #eaeaea;
                .icon-miniPro {
                  position: absolute;
                  left: 2px;
                  top: 5px;
                  width: 12px;
                  height: 12px;
                  background: url('~@/assets/icon-mini-pro.png') no-repeat center;
                  background-size: 100% 100%;
                  font-size: 12px;
                  color: #999;
                  line-height: 24px;
                }
              }
            }
          }
          
        }
        
      }
      
      .miniPro-shareImg {
        position: relative;
        .set-name {
          .dashed-border {
            width: 598px;
          }
        }
        .miniPro-preview {
          width: 300px;
          display: inline-block;
          .miniPro-pre-img {
            width: 218px;
            border-radius: 6px;
            overflow: hidden;
            background-color: #f5f5f5;
            img {
              width: 100%;
            }
            .def-pre-img {
              width: 100%;
              height: 327px;
              background: url('~@/assets/bg-minipic.png') no-repeat center;
              background-size: 100% 100%;
            }
          }
        }
        .upload-img {
          .upload-desc {
            position: absolute;
            right: 0;
            top: 40px;
          }
        }
      }
    }
  }
}