.login-form {
  width: 460px;
  // height: 310px;
  background: #FFF;
  box-shadow: 0 4px 10px 0 rgba(0,0,0,0.30);
  border-radius: 10px;
  padding: 50px 80px 10px 80px;
  .ant-form-item {
    margin-top: 20px;
    margin-bottom:0px;
  }

  .ant-input {
    height: 40px;
    box-shadow: 0 5px 6px 0 rgba(100,133,255,0.13);
    border-radius: 2px;
    &::placeholder {
      font-size: 14px;
    }
  }
  #uuid{
    width:105px;
  }
  .ant-input-affix-wrapper .ant-input:not(:first-child) {
    padding-left: 42px;
  }
  .ant-input-affix-wrapper .ant-input-prefix {
    left: 14px;
  }
  .icon-login-user {
    width: 20px;
    height: 20px;
    background: url('~@/assets/icon-login-user.png') no-repeat center;
    background-size: 20px 20px;
  }
  .icon-login-pwd {
    width: 20px;
    height: 20px;
    background: url('~@/assets/icon-login-pwd.png') no-repeat center;
    background-size: 20px 20px;
  }
  .login-form-forgot {
    float: right;
    margin-top: -5px;
    margin-bottom: 20px;
    font-size: 12px;
    height: 17px;
    line-height: 17px;
  }
  .login-form-button {
    width: 100%;
    height: 40px;
    margin-bottom: 10px;
    background-image: linear-gradient(90deg, #8F3CFE 0%, #4305FB 100%);
    // background-color: #6485FF;
  }
  .ant-checkbox-wrapper {
    font-size: 12px;
    color: #666;
  
  }
  .employee-login{
    margin-left:107px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #666666;
    text-align: justify;
   
  }
  .update{
    color: #4305FB;
    float:right;
    cursor:pointer;
  }
  .checkPassWord{
    color:#f5222d;
    font-size:14px;
  }
  .checkUser{
    color:#f5222d;
    font-size:14px;
  }

}