.ant-modal-body {
  height: 510px;
  width: 690px;
  position: relative;
}
.ant-modal-body .popup-box {
  height: 510px;
  width: 690px;
  position: absolute;
  top: 0;
  left: 0;
}
.ant-modal-body .popup-box .popup-content .popup-leftBox {
  width: 312px;
  height: 510px;
  float: left;
  background: #E4E4E4;
}
.ant-modal-body .popup-box .popup-content .popup-leftBox .leftBox-img {
  width: 272px;
  height: 470px;
  margin-top: 20px;
  margin-left: 20px;
}
.ant-modal-body .popup-box .popup-content .popup-rightBox {
  width: 378px;
  height: 510px;
  background: #fff;
  float: left;
}
.ant-modal-body .popup-box .popup-content .popup-rightBox .rightBox-content {
  width: 339px;
  height: 510px;
  padding-top: 0.1px;
}
.ant-modal-body .popup-box .popup-content .popup-rightBox .rightBox-content .Scan-box {
  width: 190px;
  height: 230px;
  margin: 100px 90px 0 60px;
  background: #FFFFFF;
  border: 1px solid #D9D9D9;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 11px 13px 0 11px;
}
.ant-modal-body .popup-box .popup-content .popup-rightBox .rightBox-content .Scan-box .Scan-box-img {
  width: 170px;
  height: 170px;
  margin: 11px auto 13px;
}
.ant-modal-body .popup-box .popup-content .popup-rightBox .rightBox-content .Scan-box .wxScan {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #333333;
  line-height: 20px;
  height: 20px;
  text-align: center;
  margin-top: 10px;
}
.ant-modal-body .popup-box .popup-content .popup-rightBox .rightBox-content .Scan-box .wxScan .scan {
  display: inline-block;
}
