.newActive {
  min-height: 100%;
}
.newActive .pl80 {
  padding-left: 80px;
}
.newActive .resetBg {
  background-color: #FAFAFA;
}
.newActive .category-one,
.newActive .category-two {
  padding: 10px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  text-align: justify;
}
.newActive .category-one {
  padding: 10px;
  color: #666666;
}
.newActive .category-two {
  color: #B3B3B3;
}
.newActive .gameList {
  overflow: scroll;
  overflow-x: hidden;
}
.newActive .gameList .game-content {
  padding-top: 12px;
  padding-left: 72px;
}
.newActive .gameList .detail {
  width: 240px;
  height: 200px;
  display: inline-block;
  margin: 10px;
  padding: 10px 10px 15px;
  background: #FFFFFF;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}
.newActive .gameList .detail .game-gif {
  display: none;
}
.newActive .gameList .detail .game-img {
  display: block;
  width: 220px;
  height: 130px;
}
.newActive .gameList .detail .botttom {
  height: 32px;
  line-height: 32px;
  margin-top: 15px;
}
.newActive .gameList .detail .botttom .name {
  float: left;
}
.newActive .gameList .detail .botttom .create-btn {
  float: right;
}
.newActive .gameList .detail:hover .game-gif {
  display: block;
  width: 220px;
  height: 130px;
}
.newActive .gameList .detail:hover .game-img {
  display: none;
}
.newActive .pagination {
  text-align: center;
}
.ant-btn-primary {
  border-color: #6A3FF3;
  background-color: #6A3FF3;
  border-radius: 6px;
}
.ant-btn-primary:hover,
.ant-btn-primary:focus {
  color: #fff;
  background-color: #6A3FF3;
  border-color: #6A3FF3;
}
