.ant-modal-body{
   height: 510px;
   width:690px;
   position: relative;
   .popup-box{
    height: 510px;
    width:690px;
    position: absolute;
        top: 0;
        left: 0;
       .popup-content{
         .popup-leftBox{
             width:312px;
             height:510px;
             float:left;
             background: #E4E4E4;
             .leftBox-img{
                width:272px;
                height:470px;
                margin-top:20px;
                margin-left:20px;
             }
          }
         .popup-rightBox{
            width:378px;
            height:510px;
            background:#fff;
            float:left;
            .rightBox-content{
                position: relative;
                width:339px;
                height:510px;
                margin-left:39px;
                padding-top: 0.1px;
                .game-name-box{
                    font-family: PingFangSC-Semibold;
                    font-size: 20px;
                    color: #333333;
                    text-align: justify;
                    margin-top:44px;
                    height:28px;
                    
                    .game-name-img{
                        float: left;
                        width:24px;
                        height:18px;
                        background:url("../../assets/game.png") no-repeat center;
                        margin:5px auto;
                        margin-right: 4px;
                        line-height:28px;
                        
                    }
                    .game-name{
                        float: left;
                    }

                }
                .split-line{
                    width:300px;
                    height:1px ;
                    background: #D9D9D9;
                    border-radius: 4px;
                    margin-top:10px;
                }
                .actExpTitle_New{
                    font-family: PingFangSC-Semibold;
                    font-size: 14px;
                    color: #333333;
                    margin-top:30px;
                    line-height: 20px;
                    height:20px;
                   

                    .actExpTitle_mark{
                        float:left;
                        width:6px;
                        height:6px;
                        background: #91DE83;
                        border-radius:50%;
                        margin:7px auto;
                        margin-right:8px;
                    }
                    .actExpTitle_name{
                        float:left;
                    }
                }
                .actExp_New{
                    width:300px;
                    font-family: PingFangSC-Regular;
                    font-size: 14px;
                    color: #666666;
                    text-align: justify;
                    line-height: 20px;
                    margin-top:6px;
                }
                .marketingTitle_New{
                    font-family: PingFangSC-Semibold;
                    font-size: 14px;
                    color: #333333;
                    margin-top:30px;
                    line-height: 20px;
                    height:20px; 
                    .marketing_mark{
                        float:left;
                        width:6px;
                        height:6px;
                        background: #FF7EC6;
                        border-radius:50%;
                        margin:7px auto;
                        margin-right:8px;
                    }
                    .marketing_name{
                        float:left;

                    }
                }
                .marketing_New{

                    font-family: PingFangSC-Regular;
                    font-size: 12px;
                    color: #6485FF;
                    margin-top:4px;
                    width:300px;
                    overflow: hidden;
                    div{
                        float: left;
                        margin-right: 8px;
                    }
                    .marketing{
                   
                      padding:5px 10px;
                      background: rgba(100,133,255,0.10);
                      border: 1px solid rgba(100,133,255,0.50);
                      border-radius: 4px;
                      margin-top:5px;
                      margin-bottom:5px;
                     
                    }
                }
                .createBtn{
                    width:300px;
                    height:40px;
                    color:#fff;
                    background: #6485FF;
                    border-radius: 2px;
                    font-family: PingFangSC-Regular;
                    font-size: 16px;
                    text-align:center;
                    line-height:40px;
                    position:absolute;
                    top:430px;
                    left:0;
                    cursor:pointer;
                }

                
                
            }
          }

           

            
       }
   }
}