.Edit {
  height: 100%;
  position: relative;
  min-width: 1334px;
  .edit-header {
    position: relative;
    z-index: 999;
    height: 64px;
    // border-bottom: 1px solid #ccc;
    box-shadow: 0 0 10px 0 rgba(0,0,0,.1);
    .save-btn,
    .close-btn {
      width: 77px;
      height: 28px;
      position: absolute;
      top: 20px;
      padding: 0;
      font-size: 12px;
    }
    .icon-save,
    .icon-close {
      width: 10px;
      height: 10px;
      display: inline-block;
      margin-right: 8px;
      background-color: #ccc;
    }
    .save-btn {
      right: 140px;
      .icon-save {
        
      }
    }
    .ant-btn:hover, 
    .ant-btn:focus {
      color: rgba(0, 0, 0, 0.65);
      border-color: #D9D9D9;
    }
    // button[ant-click-animating-without-extra-node]:after {
    //     border: 0 none;
    //     opacity: 0;
    //     animation:none 0 ease 0 1 normal;
    //   }
    .close-btn {
      right: 30px;
      .icon-close {
        
      }
    }
    .logo {
      position: absolute;
      left: 15px;
      top: 6px;
      width: 100px;
      height: 48px;
      background: url('~@/assets/logo.png') no-repeat center;
      background-size: 100% 100%;
    }
  }
  .content {
    height: calc(100% - 64px);
    position: absolute;
    top: 64px;
    left: 0;
    right: 0;
    .ant-layout-sider {
      border-right: 1px solid #D9D9D9;
      .side-cont {
        margin: 0 35px;
        font-size: 16px;
        color: #b3b3b3;
        text-align: center;
        .side-item {
          cursor: pointer;
          height: 50px;
          line-height: 50px;
          border-bottom: 1px dashed #CBCBCB;
          &.active {
            font-size: 18px;
            color: #333;
          }
        }
      }
    }
    .ant-layout-content {
      background-color: #fff;
      display: flex;
      .content-left {
        width: 360px;
        // height: 100%;
        // flex: 1;
      }
      .content-edit {
        // float: right;
        // width: 830px;
        flex: 1;
        min-width: 830px;
        height: 100%;
        box-shadow: -10px 0 10px -8px rgba(0, 0, 0, 0.1);
        padding-left: 30px;
        overflow: auto;
        > .ant-tabs {
          > .ant-tabs-bar {
            border-bottom: 1px solid #D9D9D9;
            margin-bottom: 20px;
            > .ant-tabs-nav-container {
              > .ant-tabs-nav-wrap {
                > .ant-tabs-nav-scroll {
                  > .ant-tabs-nav {
                    .ant-tabs-tab {
                      padding: 20px 0 23px;
                      font-size: 16px;
                    }
                    .ant-tabs-tab-active,
                    .ant-tabs-tab:hover {
                      color: #6485FF;
                    }
                    .ant-tabs-ink-bar {
                      background-color: #6485FF;
                      height: 3px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  
} 