.header {
  position: absolute;
  z-index: 999;
  left:0;
  top: 0;
  width: 100%;
  height: 64px;
  display: flex;
  justify-content: flex-end;
  padding: 0 20px;
  align-items: center;
  background: #FFFFFF;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.10);

  // :global {
  .logo {
    position: absolute;
    left: 15px;
    top: 6px;
    width: 100px;
    height: 48px;
    background: url('~@/assets/logo.png') no-repeat center;
    background-size: 100% 100%;
  }
}