.newActive {
  min-height: 100%;

  .pl80 {
    padding-left: 80px;
  }

  .resetBg {
    background-color: #FAFAFA;
  }

  .category-one,
  .category-two {
    padding: 10px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    text-align: justify;
  }

  .category-one {
    padding: 10px;
    color: #666666;
  }

  .category-two {
    color: #B3B3B3;
  }

  .gameList {
    overflow: scroll;
    overflow-x: hidden;

    .game-content {
      padding-top: 12px;
      padding-left: 72px;
    }

    .detail {
      width: 240px;
      height: 200px;
      display: inline-block;
      margin: 10px;
      padding: 10px 10px 15px;
      background: #FFFFFF;
      box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.05);
      border-radius: 6px;

      .game-gif {
        display: none;
      }

      .game-img {
        display: block;
        width: 220px;
        height: 130px;
      }



      .botttom {
        height: 32px;
        line-height: 32px;
        margin-top: 15px;

        .name {
          float: left;
        }

        .create-btn {
          float: right;
        }
      }

      &:hover {
        .game-gif {
          display: block;
          width: 220px;
          height: 130px;
        }

        .game-img {
          display: none;
        }
      }
    }
  }

  .pagination {
    text-align: center;
  }
}

.ant-btn-primary {
  border-color: #6A3FF3;
  background-color: #6A3FF3;
  border-radius: 6px;
}

.ant-btn-primary:hover,
.ant-btn-primary:focus {
  color: #fff;
  background-color: #6A3FF3;
  border-color: #6A3FF3;
}